<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="food-container" @click="foodClick">
      {{nowFood}}
    </div>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'HelloWorld',
  data() {
    return {
      nowFood: '',
      allFoods: [],
      intervalState: true,
      nowInterval: 0
    }
  },
  props: {
    msg: String
  },
  mounted() {
    this.getAllFoods()
    this.startInterval()
  },
  methods: {
    getAllFoods() {
      axios.get('https://challengezhou.github.io/common-server/z/foods').then((res) => {
        let foods = res.data;
        foods.split('\n').forEach(food => {
          if (food) {
            this.allFoods.push(food)
          }
        });
        console.log('数据：',  this.allFoods);
      })
    },
    startInterval() {
      let i = 0
      this.nowInterval = setInterval(()=> {
        this.nowFood = this.allFoods[i%this.allFoods.length]
        i++
      },80)
    },
    foodClick() {
      console.log(this.nowInterval)
      if (this.intervalState) {
        clearInterval(this.nowInterval)
        this.intervalState = false
      } else {
        this.startInterval()
        this.intervalState = true
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.food-container {
  display: inline-block;
  border-style: solid;
  height: 50px;
  width: 40%;
  text-align: center;
  font-size: 200%;
}
</style>



